import React, { Component } from 'react';
// import { AUTOCOMPLETE } from "../../../../actions/manageFeedsAction";
import AccountPicker from '../../Elements/AccountPicker';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Dropdown } from 'react-bootstrap';
import { debounce } from '../../../../../Services/Utils/utils';
import InputField from '../../Elements/InputField';
import CheckBoxField from '../../Elements/CheckboxField';
import InfoSection from '../../Elements/InfoSection';
import { autoComplete } from '../../../../../Services/Actions/addFeedAction';
import ShowHideOption from '../../Elements/ShowHideOption';

const FeedIcon = ({ iconId }) => {
    switch (iconId) {
        case 2: // Hashtag
            return <i className="ri-hashtag icon-5"></i>
            break;
        case 1:
        case 7: // Handle
            return <i className="ri-at-line icon-5"></i>
            break;
        case 3: // List
            return <i className="ri-list-check icon-5"></i>
            break;
        case 5: // Advance
            return <i className="ri-search-line icon-5"></i>
            break;
        case 9: // Text
            return <i className="ri-text icon-5"></i>
            break;
        default:
            break;
    }
}
class TwitterTabs extends Component {
    state = {
        showList: false,
        lists: [],
        spinnerActive: false, 
        errorMsg : 'This field is required.'
    }

    autoComplete = debounce((e) => {
        autoComplete({
            networkId: 1,
            handle: e.target.value,
        }).then(res => {
            const { data } = res
            if (data.response_code == 200) {
                this.setState({ lists: data.responseData.data, showList: true, spinnerActive: false })
            }
            else this.setState({ lists: [], showList: false, spinnerActive: false })
        }).catch(ex => {
            if (ex.response) {
                const { data } = ex.response;
                this.setState({
                    errorMsg: data.message
                })
            }
            this.setState({ spinnerActive: false })
        })
    })

    handleAutoComplete = (e) => {
        this.props.onChangeHandler(e);
        this.autoComplete(e);
        this.setState({ spinnerActive: true })
    }

    onSelectList = (item) => {

        const { onListNameChange, onListIdChange } = this.props
        onListNameChange(item.name);
        onListIdChange(item.id, item.owner_id);
        this.setState({ showList: false })
    }

    render() {
        const { lists, showList, spinnerActive, errorMsg } = this.state
        const { hashtag, handle, networkId, onConnectedAccountChange, userName, showListError, listNameError, listName, advanced, tab, error, handleError, mentionError, advanceError, multiPhoto, listname, excludeRetweets, onChangeHandler, excludeReplies, onCheckHandler, mentions, favorites, favoriteError, connectedAccountList, filter } = this.props;

        return (
            <>
                <div className="mb-2 mb-xl-3">
                    <div className="">
                        {tab != 'list' ?
                            <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                    <FeedIcon iconId={filter} />
                                </span>
                                <InputField name={tab} className={`form-control form-control-lg fw-bold px-9 ${(tab == "hashtag" && error) || (handleError && tab == "handle") || (mentionError && tab == "mentions") || (favoriteError && tab == "favorites") || (advanceError && tab == "advanced") ? 'is-invalid' : ''}`} placeholder={tab !== "advanced" ? `Enter ${tab !== 'favorites' ? tab : 'the twitter handle of the user to import Liked Posts from.'}` : 'Query'} value={tab == "hashtag" ? hashtag : tab == "handle" ? handle : tab == "mentions" ? mentions : tab == "advanced" ? advanced : favorites} onChangeHandler={onChangeHandler} />
                                {(tab == "hashtag" && error) || (handleError && tab == "handle") || (mentionError && tab == "mentions") || (favoriteError && tab == "favorites") || (advanceError && tab == "advanced") ? <span className="invalid-feedback">This field is required.</span> : null}
                            </div>
                            :

                            <div className="row">
                                <div className="col-sm-6">
                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                        <Dropdown className={`w-100 ${showListError ? 'is-invalid' : ''}`} autoClose="outside">
                                            <Dropdown.Toggle variant='link' className="p-0 text-decoration-none w-100 arrow_disabled position-relative">
                                                <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                    <FeedIcon iconId={9} />
                                                </span>
                                                <InputField name={"userName"} placeholder="Enter Keyword" className={`form-control form-control-lg fw-bolder px-9`} value={userName} onChangeHandler={(e) => this.handleAutoComplete(e)} autoComplete={true} />
                                                {spinnerActive ? <div className="spinner" style={{ top: -22 }}></div> : null}
                                            </Dropdown.Toggle>
                                            {showList ? lists && lists.length > 0 ?
                                            <Dropdown.Menu className="w-100">
                                                <PerfectScrollbar options={{ wheelPropagation: false }}>
                                                    <div className="feedDropdown">
                                                        {lists.map((item, index) => (
                                                            <Dropdown.Item key={index} onClick={() => this.onSelectList(item)}>
                                                                <div className="d-flex flex-column justify-content-start">
                                                                    <p className="fw-bold fs-8 mb-0">{item.name}</p>
                                                                    <span className="fs-10 mb-0">https://twitter.com/{item.name}</span>
                                                                </div>
                                                            </Dropdown.Item>
                                                        ))}
                                                    </div>
                                                </PerfectScrollbar>
                                            </Dropdown.Menu>
                                            : null : null}
                                        </Dropdown>
                                        {showListError && <span className="invalid-feedback">{errorMsg}</span>}
                                    </div>
                                </div>
                                <div className="col-sm-6">
                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                            <FeedIcon iconId={7} />
                                        </span>
                                        <InputField name={"listName"} placeholder="Enter Username" className={`form-control form-control-lg fw-bold px-9 ${listNameError ? 'is-invalid' : ''}`} value={listName} onChangeHandler={onChangeHandler} autoComplete={true} />
                                        {listNameError && <span className="invalid-feedback">This field is required.</span>}
                                    </div>
                                </div>
                            </div>}
                        {(tab == "hashtag" && error) || (handleError && tab == "handle") || (mentionError && tab == "mentions") || (favoriteError && tab == "favorites") || (advanceError && tab == "advanced") ? <span className="invalid-feedback">This field is required</span> : null}

                        {tab == "advanced" ? <p className="fs-8 fw-medium text-gray-700 mb-1">Use the table below to construct your query.</p> : null}
                        {
                            tab == "advanced" ? <div className="card mb-5">
                                <div className='card-body'>
                                    <div className="table-responsive" style={{ height: 200 }}>
                                        <table className="table table-light mb-0">
                                            <thead>
                                                <tr>
                                                    <th style={{ width: "20%" }}><strong>Filter By:</strong></th>
                                                    <th><strong>Example</strong></th>
                                                    <th><strong>Targets Tweets:</strong></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <i className="ri-text icon-6 text-gray-800 me-1"></i>
                                                            Keywords
                                                        </div>
                                                    </td>
                                                    <td>grapes banana</td>
                                                    <td>containing both "grapes" and "banana".</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>"good time"</td>
                                                    <td>containing the exact phrase "good time"</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>die OR alive</td>
                                                    <td>containing either "die" or "alive" (or both)</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>food -drink</td>
                                                    <td>containing "food" but not "drink"</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>#soul</td>
                                                    <td>containing the hashtag "#soul"</td>
                                                </tr>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <i className="ri-user-line icon-6 text-gray-800 me-1"></i>
                                                            User
                                                        </div>
                                                    </td>
                                                    <td>from:tagbox</td>
                                                    <td>sent from the user @tagbox</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>to:tagbox</td>
                                                    <td>sent to the user @tagbox</td>
                                                </tr>
                                                <tr>
                                                    <td><strong></strong></td>
                                                    <td>@tagbox</td>
                                                    <td>mentioning @tagbox</td>
                                                </tr>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <i className="ri-link icon-6 text-gray-800 me-1"></i>
                                                            Content
                                                        </div>
                                                    </td>
                                                    <td>funny filter:links</td>
                                                    <td>containing " funny" and with a URL.</td>
                                                </tr>
                                                <tr>
                                                    <td>
                                                        <div className="d-flex fw-medium">
                                                            <i className="ri-calendar-event-line icon-6 text-gray-800 me-1"></i>
                                                            Dates
                                                        </div>
                                                    </td>
                                                    <td>champion&amp;start_time=2023-05-11T05:00:01.340Z</td>
                                                    <td>containing "champion" and sent since date "2023-05-11T05:00:01.340Z" (year-month-day).</td>
                                                </tr>
                                                <tr>
                                                    <td></td>
                                                    <td>champion&amp;end_time=2023-05-17T05:00:01.340Z</td>
                                                    <td>containing "champion" and sent before the date "2023-05-17T05:00:01.340Z".</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div> : null
                        }
                    </div>
                </div>
                <div className="pb-2">
                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={multiPhoto} onChangeHandler={onCheckHandler} name={"multiPhoto"}>
                            <span className="form-check-label">Add Multiple Photos</span>
                        </CheckBoxField>
                    </label>
                    {tab != 'list' && tab != "advanced" ? <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={excludeRetweets} onChangeHandler={onCheckHandler} name={"excludeRetweets"}>
                            <span className="form-check-label">Exclude Retweets</span>
                        </CheckBoxField>
                    </label> : null}
                    {tab != 'favorites' && tab != 'list' ? <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={excludeReplies} onChangeHandler={onCheckHandler} name={"excludeReplies"}>
                            <span className="form-check-label">Exclude Replies</span>
                        </CheckBoxField>
                    </label> : null}
                    <ShowHideOption onChangeHandler={onCheckHandler}/>
                </div>
                {(connectedAccountList && connectedAccountList.length > 0) && <AccountPicker connectedAcc={connectedAccountList} networkId={networkId} onConnectedAccountChange={onConnectedAccountChange} />}
                {tab != 'list' && tab != "advanced" && tab != "favorites" ?
                    <InfoSection text={tab == 'hashtag' ? `Only 10 tweets will be aggregated from the last 7 days' data, after which all the upcoming tweets will be aggregated.` : tab == 'handle' ? `Initially, 10 tweets will be aggregated from the Twitter handle, after which all the upcoming tweets will be aggregated.` : tab == 'mentions' ? `Only 10 mentions from the last 7 days will be aggregated, after which all the upcoming tweets will be aggregated.` : ``} />
                    : null
                }
            </>
        )
    }
}

export default TwitterTabs

