import React, { Component } from 'react';
import { saveAs } from 'file-saver';
import { connect } from 'react-redux';
import { Nav, Tab, Collapse } from "react-bootstrap";
import { capitalizeFirstLetter } from '../../../../Services/Utils/utils';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import InputField from '../Elements/InputField';
import CheckBoxField from '../Elements/CheckboxField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { createFeedAction } from '../../../../Services/Actions/addFeedAction';
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import ShowHideOption from '../Elements/ShowHideOption';



const channel = new BroadcastChannel('connect');
class Rss extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            feedUrl: "",
            moderation: false,
            error: false,
            urlError: false,
            loading: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype,
            sampleXml: false,
            isRequest: false
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, urlError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onManuallyApproveChange = (value) => this.setState({ moderation: value })

    onSubmit = () => {
        const { name, feedUrl, moderation, selectedFilterTab, selectedFilterId } = this.state;
        const { collection, networkId } = this.props;

        if (name == '') {
            this.setState({ error: true });
        } else if (feedUrl == '') {
            this.setState({ urlError: true });
        }
        else {
            var collectionId = []
            if (collection && collection.length > 0) {
                collection.map((iCollection) => {
                    collectionId.push(iCollection.value)
                })
            }
            const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
            var obj = {
                ...isCollection,
                networkId: networkId,
                filter: selectedFilterId,
                moderation: moderation ? 1 : 0,
                fieldtitle: name,
            }

            if (!feedUrl.includes("://")) {
                this.setState({ urlError: true });
                return;
            } else {
                obj['field'] = feedUrl;
            }

            this.setState({ error: false, urlError: false, loading: true });

            createFeedAction(obj, networkId, (getFeedId) => {
                this.props.getAllfeeds()
                this.setState({ loading: false });
                this.props.closeAddFeed();
                if (getFeedId) {
                    if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                    else {
                        if (String(window.location.href).includes("assets")) {
                            channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                        }
                        setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                    }


                }
            });


        }
    }

    feedIcon = (iconId) => {
        switch (iconId) {
            case 18: // Link
                return <i className="ri-link icon-4"></i>
                break;
            case 16: // Text
                return <i className="ri-text icon-4"></i>
                break;
            default:
                break;
        }
    }
    onClickToDwonloadSample = event => {
        this.setState({ isRequest: true }, () => {
            saveAs(baseUrl("media/downloads/sample-xml.txt"), `sample-xml.txt`);
            setTimeout(() => this.setState({ isRequest: false }), 2000)
        })


    }

    render() {
        const { filterTabs } = this.props;
        const { name, feedUrl, moderation, urlError, error, loading, selectedFilterTab, sampleXml, isRequest } = this.state
        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        {filterTabs && Object.keys(filterTabs).length > 1 ?
                            <div className="w-100">
                                <h6 className="fs-7 mb-3">Select the type of posts</h6>
                                <div className="card card-custom gutter-b">
                                    <div className="card-body px-xxl-5">
                                        <div className="">

                                            <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                                {
                                                    filterTabs && filterTabs.map((filterTab, index) => {
                                                        const { feedName, feedtype, filter } = filterTab;
                                                        return <Nav.Item className="p-1" key={index}>
                                                            <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                                <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                                    {this.feedIcon(filter)}
                                                                </span>
                                                                {capitalizeFirstLetter(feedtype)}
                                                            </Nav.Link>
                                                        </Nav.Item>
                                                    })
                                                }
                                            </Nav>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            : null}
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab} Details</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <Tab.Content>
                                        <Tab.Pane eventKey={"RSS"}>
                                            <div className='row'>
                                                <div className="col-sm-6">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(16)}
                                                        </span>
                                                        <InputField name={"name"} className={`form-control form-control-lg fw-bold px-9 ${error ? 'is-invalid' : ''}`} placeholder="Display Name" value={name} onChangeHandler={this.onChangeHandler} />
                                                        {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                <div className="col-sm-6">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(18)}
                                                        </span>
                                                        <InputField name={"feedUrl"} className={`form-control form-control-lg fw-bold px-9 ${urlError ? 'is-invalid' : ''}`} placeholder="Feed URL" value={feedUrl} onChangeHandler={this.onChangeHandler} />
                                                        {urlError ? <span className="invalid-feedback">This field must contain a url</span> : null}
                                                    </div>
                                                </div>
                                            </div>
                                        </Tab.Pane>
                                    </Tab.Content>
                                    <div className="d-flex w-100 justify-content-end mt-2">
                                    <ShowHideOption onChangeHandler={this.onChangeHandler} />
                                        <button type="button" className="btn p-0 text-decoration-underline" onClick={() => this.setState({ sampleXml: !sampleXml })}>
                                            <span className="text-right fw-semibold">Download Sample Format</span>
                                        </button>
                                    </div>
                                    <Collapse in={sampleXml}>
                                        <div>
                                            <div className="block-center flex-column m-auto py-3" style={{ maxWidth: 530 }}>
                                                <img className="h-auto w-100" src={baseUrl("media/images/demo/sample-design--xml-rss-feed.png")} height="600" width="300" alt="sample Rss" />
                                                <div className={`btn btn-sm btn-secondary ${isRequest ? `spinner disabled` : ``}`} download onClick={this.onClickToDwonloadSample}>
                                                    <i className="ri-download-2-line"></i>
                                                    Download Sample
                                                </div>
                                            </div>
                                        </div>
                                    </Collapse>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Rss)