
import React, { Component, useEffect } from 'react';
import { connect } from 'react-redux';
import Airbnb from '../../../../Feeds/Airbnb/Airbnb';
import Flickr from '../../../../Feeds/Flickr/Flickr';
import { getConnectedAccount } from '../../../../../../Services/Actions/addFeedAction';
import Tumblr from '../../../../Feeds/Tumblr/Tumblr';
import Pinterest from '../../../../Feeds/Pinterest/Pinterest';
import Giphy from '../../../../Feeds/Giphy/Giphy';
import Rss from '../../../../Feeds/Rss/Rss';
import Yelp from '../../../../Feeds/Yelp/Yelp';
import LinkedIn from '../../../../Feeds/Linkedin/Linkedin';
import YoutubeFeed from '../../../../Feeds/Youtube/Youtube';
import YammerFeed from '../../../../Feeds/Yammer/Yammer';
import Tiktok from '../../../../Feeds/Tiktok/Tiktok';
import Vimeo from '../../../../Feeds/Vimeo/Vimeo';
import TwitterFeed from '../../../../Feeds/Twitter/Twitter';
import GoogleFeed from '../../../../Feeds/Google/Google';
import SlackFeed from '../../../../Feeds/Slack/Slack';
import FacebookFeed from '../../../../Feeds/Facebook/Facebook';
import Instagram from '../../../../Feeds/Instagram/Instagram';




const CreateFeed = (props) => {
    const { filterTabs, selectedNetwork, collection, getConnectedAccount, authRequire, onBack, id, history, editMediaHandleCallback } = props;
    let isAuthRequired = authRequire.includes(id);
    useEffect(() => {
        if (id == 18) {
            getConnectedAccount(2);
        }
        getConnectedAccount(id);
    }, [props.id])

    switch (id) {
        case 1:
            return filterTabs ? <TwitterFeed filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} isAuthRequired={isAuthRequired} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 18:
            return filterTabs ? <Instagram filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} isAuthRequired={isAuthRequired} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 3:
            return filterTabs ? <FacebookFeed filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} isAuthRequired={isAuthRequired} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 5:
            return filterTabs ? <Pinterest filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 23:
            return filterTabs ? <Airbnb filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 7:
            return filterTabs ? <YoutubeFeed filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 11:
            return filterTabs ? <Tumblr filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 12:
            return filterTabs ? <Rss filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 6:
            return filterTabs ? <Flickr filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 4:
            return filterTabs ? <GoogleFeed filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} isAuthRequired={isAuthRequired} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 8:
            return filterTabs ? <Vimeo filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} isAuthRequired={isAuthRequired} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 28:
            return filterTabs ? <Tiktok filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 10:
            return filterTabs ? <LinkedIn filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 19:
            return filterTabs ? <Yelp filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 21:
            return filterTabs ? <YammerFeed filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 20:
            return filterTabs ? <SlackFeed filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} networkData={selectedNetwork} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        case 26:
            return filterTabs ? <Giphy filterTabs={filterTabs} collection={collection} networkId={selectedNetwork ? selectedNetwork.id : 0} onBack={onBack} history={history} closeAddFeed={() => editMediaHandleCallback(false)} /> : null

        default:
            return <>network id - {id}</>
    }
}
class AddFeedByNetwork extends Component {

    render() {
        const { selectedNetwork } = this.props;
        return selectedNetwork ? <CreateFeed id={selectedNetwork.id} {...this.props} /> : null
    }
}

const mapStateToProps = (state) => {
    return {
        authRequire: state.addFeed.authRequire
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getConnectedAccount: (id) => dispatch(getConnectedAccount(id)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AddFeedByNetwork);