import React, { Component } from 'react';

class CheckBoxField extends Component {
    render() {
        const { isChecked, onChangeHandler, name } = this.props;
        return <>
            <input className="form-check-input" type="checkbox" name={name} checked={isChecked} onChange={onChangeHandler} />
            {this.props.children}
        </>
    }
}

export default CheckBoxField;