import { Component } from "react";
import { connect } from 'react-redux';
import { Nav, Tab } from 'react-bootstrap';
import { capitalizeFirstLetter, showAddAccountAlert } from "../../../../Services/Utils/utils";
import { addUserAccount, createFeedAction, selectAccount } from "../../../../Services/Actions/addFeedAction";
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import InstagramFeedTab from "./components/InstagramTab";
import SubmitFeedRequest from "../Elements/SubmitFeedRequest";
import ConnectAccount from "../Elements/ConnectAccount";


const channel = new BroadcastChannel('connect');

class InstagramFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hashtag: "",
            handle: '',
            error: false,
            hashtagPost: false,
            excludeVideo: false,
            onlyReels: false,
            hanldeError: false,
            account: null,
            connetedAccount: null,
            feedObj: {},
            selectedChildAccount: {},
            personalAcc: {},
            accountList: [],
            isAuthRequired: props.isAuthRequired,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype,
            moderation:false
        }
    }


    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }

    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, urlError: false });
    }

    onPersonalAccChange = (value) => this.setState({ personalAcc: value })
    onSelectedChildAccountChange = (value) => this.setState({ selectedChildAccount: value })
    onCheckHandler = event => { this.setState({ [event.target.name]: event.target.checked }) }
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })

    addInstaAccount = () => {
        const { networkData } = this.props;
        this.setState({ feedObj: this.createFeedPostData() });

        //need to be checked
        if ((this.state.selectedFilterTab == "personal")) {
            showAddAccountAlert({ ...networkData, id: 2, name: 'Facebook' });
            return;
        }

        showAddAccountAlert(networkData);
    }



    createFeedPostData = () => {
        const { onlyReels, excludeVideo, hashtagPost, hashtag, handle, personalAcc, selectedChildAccount, moderation, selectedFilterId } = this.state;
        const { collection, accountId } = this.props;
        var collectionId = []
        if (collection && collection.length > 0) {
            collection.map((iCollection) => {
                collectionId.push(iCollection.value)
            })
        }
        const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
        var obj = {
            ...isCollection,
            networkId: selectedFilterId == 1 ? 2 : 18,
            feedText: [34, 24, 25, 66].includes(selectedFilterId) ? (selectedChildAccount && Object.keys(selectedChildAccount).length > 0) && selectedChildAccount.label : selectedFilterId == 1 ? (personalAcc && Object.keys(personalAcc).length > 0) ? personalAcc.label : "" : selectedFilterId == 26 ? hashtag : handle,
            hashtagCaption: hashtagPost ? 1 : 0,
            filter: selectedFilterId,
            accountId: accountId,
            hashtagIGTV: excludeVideo ? 1 : 0,
            onlyReels: onlyReels ? 1 : 0,
            moderation: moderation ? 1 : 0,
        }

        if ([34, 24, 25, 66].includes(selectedFilterId)) {
            obj['accountId'] = (selectedChildAccount && Object.keys(selectedChildAccount).length > 0) ? selectedChildAccount.value : ""
        } else if (selectedFilterId == 1) {
            obj['accountId'] = (personalAcc && Object.keys(personalAcc).length > 0) ? personalAcc.value : ""
        }
        return obj;
    }


    createFeed = (connectedAccId) => {
        let obj = this.createFeedPostData();
        if (connectedAccId != null) {
            obj = { ...obj, accountId: connectedAccId }
        }

        this.setState({ loading: true, error: false, handleError: false, mentionError: false, advanceError: false, favoriteError: false, listNameError: false, showListError: false });

        createFeedAction(obj, obj.networkId, (getFeedId) => {
            this.props.getAllfeeds()
            this.setState({ loading: false, isAuthRequired: true });
            this.props.closeAddFeed();
            if (getFeedId) {
                if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                else {
                    if (String(window.location.href).includes("assets")) {
                        channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                    }
                    setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                }


            }
        });
    }

    onSubmit = () => {
        const { hashtag, handle, selectedFilterId, isAuthRequired } = this.state;
        const { accountId } = this.props;
        if ((!hashtag || hashtag == "") && selectedFilterId == 26) {
            this.setState({ error: true, loading: false })
        } else if ((!handle || handle == "") && selectedFilterId == 23) {
            this.setState({ handleError: true, isLoading: false })
        } else {
            if (isAuthRequired) {
                if (accountId) {
                    this.createFeed(null);
                } else {
                    this.addInstaAccount();
                }
            } else {
                if ([23, 26].includes(selectedFilterId)) {
                    this.createFeed(accountId != null ? accountId : 0);
                } else {
                    if (accountId) {
                        this.createFeed(null);
                    } else {
                        this.addInstaAccount();
                    }
                }
            }

        }
    }

    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId } = response;
        const { selectedFilterId } = this.state;

        //34, 24, 25, 66 filterId's
        addUserAccount(networkId, data, [34, 24, 25, 66].includes(selectedFilterId) ? (responseData) => this.setState({ accountList: responseData }) : (connectAccountId) => this.createFeed(connectAccountId, true), selectedFilterId);

    }

    createFeedFromPage = (accId, parentId, name) => {
        let { feedObj } = this.state;
        const { networkId } = this.props;

        if (networkId == 18) {

            if ([34, 24, 25, 66].includes(parseInt(feedObj.filter))) {
                feedObj.feedText = name;
            }

            feedObj.accountId = parentId;
        }

        this.setState({ loading: true, error: false, handleError: false, mentionError: false, advanceError: false, favoriteError: false, listNameError: false, showListError: false });
        createFeedAction(feedObj, networkId, (getFeedId) => {
            this.setState({ loading: false, isAuthRequired: true });
            this.props.closeAddFeed();
            if (getFeedId) {
                if (feedObj && Object.keys(feedObj).length > 0 && feedObj.collection && feedObj.collection != "" && String(feedObj.collection).split(",").length == 1) this.props.history(`/assets/${feedObj.collection}`);
                else {
                    if (String(window.location.href).includes("assets")) {
                        channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                    }
                    setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                }


            }
        });
    }

    componentDidMount() {
        channel.addEventListener("message", this.readAuthenticationCode);
    }

    componentWillUnmount() {
        channel.removeEventListener("message", this.readAuthenticationCode);
    }
    feedIcon = (iconId) => {
        switch (iconId) {
            case 26: // Hashtag
                return <i className="ri-hashtag icon-6"></i>
                break;
            case 23:
            case 24: // Handle
                return <i className="ri-at-line icon-6"></i>
                break;
            case 34: // Stories
                return <i className="ri-donut-chart-line icon-6"></i>
                break;
            case 25: // tag
                return <i className="ri-price-tag-3-line icon-6"></i>
                break;
            case 66: // Video
                return <i className="ri-movie-line icon-6"></i>
                break;
            case 1: // Account
                return <i className="ri-account-circle-line icon-6"></i>
                break;
            default:
                break;
        }
    }
    render() {
        const { error, selectedFilterTab, selectedFilterId, hashtag, handle, handleError, moderation, hashtagPost, excludeVideo, onlyReels, loading, connetedAccount, account, accountList } = this.state;
        const { filterTabs, networkId, connectedAccountList, personalAccount } = this.props;

        return !(accountList && accountList.length > 0) ? <>
            <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                <div className="addSourceTabs">
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Select the type of posts</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                    {filterTabs && filterTabs.map((filterTab, index) => {
                                        const { feedName, feedtype, filter } = filterTab;
                                        return <Nav.Item className="p-1" key={index}>
                                            <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                    {this.feedIcon(filter)}
                                                </span>
                                                {capitalizeFirstLetter(feedtype === 'favorites' ? feedName : feedtype)}
                                            </Nav.Link>
                                        </Nav.Item>
                                    })}
                                </Nav>
                            </div>
                        </div>
                    </div>
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <Tab.Content>
                                    {(filterTabs && filterTabs.length > 0) ? filterTabs.map(filter => (
                                        <Tab.Pane eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0">
                                            <InstagramFeedTab
                                                onAddAccount={this.addInstaAccount}
                                                connetedAccount={connetedAccount}
                                                onConnectedAccountChange={this.onConnectedAccountChange}
                                                tab={selectedFilterTab}
                                                hashtag={hashtag}
                                                error={error}
                                                connectedAcc={connectedAccountList}
                                                onTextChange={this.onChangeHandler}
                                                account={account}
                                                onCheckHandler={this.onCheckHandler}
                                                moderation={moderation}
                                                handle={handle}
                                                handleError={handleError}
                                                personalAccount={personalAccount}
                                                hashtagPost={hashtagPost}
                                                onlyReels={onlyReels}
                                                excludeVideo={excludeVideo}
                                                onPersonalAccChange={this.onPersonalAccChange}
                                                onSelectedChildAccountChange={this.onSelectedChildAccountChange}
                                                feedIcon={this.feedIcon}
                                            />
                                        </Tab.Pane>
                                    )) : null}
                                </Tab.Content>
                            </div>
                        </div>
                    </div>
                </div>
            </Tab.Container>
            <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
        </> :
            <ConnectAccount accountList={accountList} loading={loading} networkId={networkId} connectedAcc={connectedAccountList} filterId={selectedFilterId} createFeedFromPage={this.createFeedFromPage} onBack={() => this.setState({ accountList: [] })} />
    }
}

const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount,
        connectedAccountList: state.addFeed.connectedAcc,
        personalAccount: state.addFeed.personalAccount,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        selectAccount: (id) => dispatch(selectAccount(id)),
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(InstagramFeed)