import React, { useState } from "react";
import PlacesAutocomplete, { geocodeByAddress,getLatLng} from "react-places-autocomplete";
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Dropdown } from 'react-bootstrap';


export default function SearchPlace(props) {
  const [address, setAddress] = useState("");
  const [list, setList] = useState(false);
  const [spinnerActive, spinnerActiveFun] = useState(false);


  const handleChange = (e) => { setAddress(e); setList(true); props.onFieldTextChange("", "") };

  const handleSelect = address => {
    spinnerActiveFun(true)
    geocodeByAddress(address)
      .then(results => getLatLng(results[0]))
      .then(latLng => {
        props.onLatlanChange(`${latLng.lat},${latLng.lng}`)
        spinnerActiveFun(false)
      })
      .catch(error => console.error('Error', error));
  };

  const handleSelect1 = (v1, v2) => {
    props.onFieldTextChange(v1, v2)
    setAddress(v1);
    setList(false);
  };


  return (<PlacesAutocomplete
            value={address}
            onChange={handleChange}
            onSelect={handleSelect}
          >
        {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
            <div className="position-relative w-100">
              <i className="ri-map-pin-2-line icon-5 text-gray-500 position-absolute bottom-0 start-0 translate-middle-x mx-5 my-3"></i>
              <input {...getInputProps({ placeholder: "Type address" })} className={`form-control form-control-lg fw-bold px-9 ${props.error?'is-invalid':''}`}  autoComplete="off"/>
              {spinnerActive ? <div className="spinner spinner-gray" style={{top:-22}}></div> : null}
              {list && address.length > 0 ?
                <Dropdown className={`w-100`} show>
                  <Dropdown.Menu className="w-100">
                      <PerfectScrollbar options={ { wheelPropagation: false } }>
                        {suggestions.map(suggestion => {
                          return (<Dropdown.Item className="text-break" {...getSuggestionItemProps(suggestion)} onClick={() => { handleSelect1(suggestion.description, suggestion.placeId); handleSelect(suggestion.description) }} style={{whiteSpace:'normal'}}> {suggestion.description}</Dropdown.Item>);
                        })}
                      </PerfectScrollbar>
                  </Dropdown.Menu>
                </Dropdown>
                : ''}
            </div>
        )}
      </PlacesAutocomplete>
  );
}

