import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter } from '../../../../Services/Utils/utils';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import InputField from '../Elements/InputField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { createFeedAction } from '../../../../Services/Actions/addFeedAction';
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import { toast } from 'react-toastify';
import InfoSection from '../Elements/InfoSection';
import BulkDropzone from './components/BulkDropzone';
import ShowHideOption from '../Elements/ShowHideOption';

const channel = new BroadcastChannel('connect');
class LinkedIn extends Component {
    constructor(props) {
        super(props);
        this.state = {
            postUrl: "",
            handle: "",
            moderation: false,
            imageUrl: null,
            fileName: 'Your selected file name',
            error: false,
            handleError: false,
            loading: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, handleError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onUploadSuccess = (fileName, url) => {
        this.setState({ imageUrl: url, fileName: fileName });
    };

    onSubmit = () => {
        const { postUrl, imageUrl, moderation, selectedFilterTab, selectedFilterId } = this.state;
        const { collection, networkId } = this.props;

        if (selectedFilterTab == 'Post Url' && postUrl == '') {
            this.setState({ error: true });
        } else if (selectedFilterTab == 'Bulk URLs' && imageUrl == null) {
            toast.error("Please upload a Csv File", { autoClose: 3000 });
        }
        else {
            var collectionId = []
            if (collection && collection.length > 0) {
                collection.map((iCollection) => {
                    collectionId.push(iCollection.value)
                })
            }
            const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
            var obj = {
                ...isCollection,
                networkId: networkId,
                filter: selectedFilterId,
                moderation: moderation ? 1 : 0
            }
            if (selectedFilterId == 16) {
                obj['field'] = postUrl
            } else if (selectedFilterId == 18) {
                obj['field'] = imageUrl
            }

            this.setState({ error: false, loading: true })
            createFeedAction(obj, networkId, (getFeedId) => {
                this.props.getAllfeeds()
                this.setState({ loading: false });
                this.props.closeAddFeed();
                if (getFeedId) {
                    if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                    else {
                        if (String(window.location.href).includes("assets")) {
                            channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                        }
                        setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                    }


                }
            });

        }
    }

    feedIcon = (iconId) => {
        switch (iconId) {
            case 16: // Link
                return <i className="ri-link icon-6"></i>
                break;
            case 18: // Links
                return <i className="ri-links-line icon-6"></i>
                break;
            default:
                break;
        }
    }
    render() {
        const { filterTabs } = this.props;
        const { postUrl, fileName, moderation, error, loading, selectedFilterTab } = this.state
        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                        {
                                            filterTabs && filterTabs.map((filterTab, index) => {
                                                const { feedName, feedtype, filter } = filterTab;
                                                return <Nav.Item className="p-1" key={index}>
                                                    <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                        <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                            {this.feedIcon(filter)}
                                                        </span>
                                                        {capitalizeFirstLetter(feedtype)}
                                                    </Nav.Link>
                                                </Nav.Item>
                                            })
                                        }
                                    </Nav>
                                </div>
                            </div>
                        </div>
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">
                                        <Tab.Content>
                                            <Tab.Pane eventKey={"Post Url"}>
                                                <div className="position-relative w-100 mb-3">
                                                    <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                        {this.feedIcon(16)}
                                                    </span>
                                                    <InputField name={"postUrl"} className={`form-control form-control-lg fw-bold px-9 ${error ? 'is-invalid' : ''}`} placeholder="Enter LinkedIn Post Url" value={postUrl} onChangeHandler={this.onChangeHandler} />
                                                    {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                </div>
                                                <InfoSection text={"LinkedIn auto update is not available due to changes made by LinkedIn in MDP API Recently. However, we are providing a solution for this condition you can simply add a post URL from your company page one by one manually and display your LinkedIn post on your website."} />
                                            </Tab.Pane>
                                            <Tab.Pane eventKey={"Bulk URLs"}>
                                                <div className="d-flex w-100 justify-content-start mb-1">
                                                    <span className="text-right fw-medium">
                                                        Download the
                                                        <a href={baseUrl('media/downloads/csv-sample.csv')} target='_blank' className="text-decoration-underline mx-1 fw-semibold" download>Sample CSV template</a>
                                                        to see an example of the format required
                                                    </span>
                                                </div>
                                                <BulkDropzone onUploadSuccess={this.onUploadSuccess} fileName={fileName} />
                                                <InfoSection text={"You can upload 50 urls at one time."} />
                                            </Tab.Pane>
                                            <ShowHideOption onChangeHandler={this.onChangeHandler}/>
                                        </Tab.Content>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}
const mapStateToProps = (state) => {
    return {
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(LinkedIn)