import React, { Component } from 'react'
import Select from 'react-select'
import InputField from '../../Elements/InputField';
import CheckBoxField from '../../Elements/CheckboxField';

export default class SlackTab extends Component {
    componentDidMount() {
        const { accounts } = this.props;

        if (accounts && accounts.length > 0) {
            this.props.onSelect(accounts[0].ConnectedAccounts.id);
        }
    }
    render() {
        const { accounts, tab, moderation, onCheckHandler, onSelect, error, keyword, handleInput, selectAccount, channelError } = this.props;

        const options = accounts ? accounts.map((account) => ({ label: account.ConnectedAccounts.value2, value: account.ConnectedAccounts.id })) : [];
        return (<div className="row gx-3">
                    <div className="col-sm-6">
                        <div className="mb-2 mb-xl-3">
                            <span className={`${channelError? 'is-invalid':''}`}>
                            <Select className="selectpicker selectpicker-lg" menuPlacement="auto" classNamePrefix="selectpicker" onChange={(e) => onSelect(e.value)} options={options} value={options[options.findIndex(i => i.value == selectAccount)]} />
                            </span>
                            {channelError && <span className="invalid-feedback">This field is required</span>}
                        </div>
                    </div>
                    {tab == "ChannelKeyword" ?
                    <div className="col-sm-6">
                        <div className="mb-2 mb-xl-3">
                            <div className="position-relative w-100 mb-3">
                                <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                    <i className="ri-text icon-5"></i>
                                </span>
                                <InputField name={"keyword"} className="form-control form-control-lg fw-bolder px-9" placeholder="Enter keyword" value={keyword} onChangeHandler={handleInput} />
                                {error && <span className="invalid-feedback">This field is required</span>}
                            </div>
                        </div>
                    </div>
                    : null}
                </div>
        )
    }
}
