import React, { Component } from 'react';
import Select from 'react-select';
import { baseUrl } from "../../../../../Assets/AssetsHelper";

export default class GoogleTab extends Component {
    state = { accountOption: [], parentOption: [] }

    componentDidMount() {
        this.getChildAccountData()
    }
    componentWillReceiveProps(nextProps) {

        if (nextProps.connectedAcc !== this.state.parentOption) {
            this.getChildAccountData()
        }
    }
    getChildAccountData = () => {
        const { connectedAcc } = this.props;
        if (connectedAcc && connectedAcc.length) {
            var data = connectedAcc.map((f, i) => {
                return ({ 'label': f.ConnectedAccounts.accountName, 'value': f.ConnectedAccounts.id })
            })
            this.setState((prev) => ({ ...prev, parentOption: data }));
        }

    }

    onAccountIdChange = (value) => {
        const { childAcc } = this.props
        if (value == 0) {
            this.props.onAccountChildChange(value)
        }
        else {
            var mainAccountId = childAcc.filter(f => f.ConnectedAccounts.id == value)
            this.props.onAccountChildChange(value)
            this.props.onConnectedAccountChange(mainAccountId[0].ConnectedAccounts.parent)
        }
    }

    onParentSelected = (value) => {
        const { connectedAcc } = this.props
        this.props.onConnectedAccountChange(value);
        let childAcccounts = [];
        if (value !== 0) {
            const selectedAcc = connectedAcc.filter(account => account.ConnectedAccounts.id === value);
            childAcccounts = selectedAcc[0].ConnectedAccounts.child.map((f) => {
                return ({ 'label': f.ConnectedAccounts.value2, 'value': f.ConnectedAccounts.id })
            });
            childAcccounts.push({ 'label': "Other", 'value': 0 });
        }

        this.setState((prev) => ({ ...prev, accountOption: childAcccounts }));
    }


    render() {

        const { otherAccount, connetedAccount, accountChild, onotherAccount,onAddAccount } = this.props
        const { accountOption, parentOption } = this.state;

        return (
            <>
                <div className="row">
                    <div className="col-md-6">
                        <label>Select Account</label>
                        <Select className="selectpicker addFeedGoogle" options={parentOption} classNamePrefix="selectpicker" defaultValue={connetedAccount} onChange={(e) => this.onParentSelected(e.value)} />
                        <br />
                    </div>

                    <div className="col-md-6">
                        <label>Select Place</label>
                        <Select className="selectpicker addFeedGoogle" options={connetedAccount ? accountOption : []} classNamePrefix="selectpicker" defaultValue={accountChild} onChange={(e) => { this.onAccountIdChange(e.value); onotherAccount(e.value == 0 ? true : false) }} />
                        <br />
                    </div>
                </div>


                {otherAccount ?
                    <div className="otherSection py-2">
                        <button type="button" className="btn btn-outline btn-lg align-items-center d-flex signInWithBrand m-auto">
                            <span className="h-40px w-40px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-light">
                                <img src={baseUrl("media/icons/social-icons/google.svg")} height={43} width={43} alt="facebook" />
                            </span>
                            <div className="overflow-hidden signIn_txt px-5">
                                <span className="text-gray-800 mb-0 fs-9 fw-medium text-ellipsis me-2">Connect Google</span>
                            </div>
                        </button>
                    </div> 
                    : null}

            </>
        )
    }
}