import React, { Component } from 'react';
import AccountPicker from '../../Elements/AccountPicker';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Dropdown } from 'react-bootstrap';
import CheckBoxField from '../../Elements/CheckboxField';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { autoComplete } from '../../../../../Services/Actions/addFeedAction';
import { debounce } from '../../../../../Services/Utils/utils';
import Select from 'react-select';
import ShowHideOption from '../../Elements/ShowHideOption';

class FacebookTab extends Component {
    state = {
        handles: [],
        allAccounts: [{ ConnectedAccounts: { id: 0, value2: 'loading...' } }],
        openSelect: false,
        selectValue: {},
        formatedList: [],
        allConnectAcc: []
    }

    componentDidMount() {
        this.getAccounts();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.connectedAcc) {
            if (this.state.allConnectAcc.length != nextProps.connectedAcc.length) {
                this.getAccounts();
            }
        }
    }

    getAccounts = () => {
        const { connectedAcc, onAccountChange } = this.props
        var allAccounts = []
        if (connectedAcc && connectedAcc.length > 0) {
            connectedAcc && connectedAcc.length && connectedAcc.map(acc => {
                if (acc.ConnectedAccounts.child && acc.ConnectedAccounts.child.length > 0) {
                    allAccounts = [...allAccounts, ...acc.ConnectedAccounts.child]
                }
                else {
                    this.setState({ allAccounts: [] })
                }
            })

            if (allAccounts && allAccounts.length > 0) {
                onAccountChange(allAccounts[0].ConnectedAccounts.id, allAccounts[0].ConnectedAccounts.parent);
                const formatedOption = this.formatOption(allAccounts);
                this.setState({ allAccounts, allConnectAcc: connectedAcc, formatedList: formatedOption, selectValue: formatedOption[0] });
            }
        } else {
            onAccountChange(null)
            this.setState({ allAccounts: [] })
        }
    }



    autoComplete = debounce((value) => {
        const obj = { networkId: 3, handle: value }
        if (value && value.length > 2) {
            autoComplete(obj).then(res => {
                const { responseData, response_code } = res.data;
                if (response_code == 200) {
                    this.setState({ handles: responseData.data, showHandle: responseData.data.length > 0 })
                }
                else this.setState({ handles: [], lists: [], showHandle: false })
            })
        } else {
            this.setState({ handles: [], lists: [], showHandle: false })
        }
    });

    handleTextChange = value => {
        this.props.onTextChange(value)
        this.autoComplete(value);
    }

    onSelectHandle = (item) => {
        const { onTextChange, tab } = this.props
        onTextChange(item.link)
        document.querySelector(`#${tab}-text`).value = item.link
        this.setState({ showHandle: false })
    }

    onAccChange = (selectedOption) => {
        const { onAccountChange } = this.props
        const { allAccounts } = this.state;
        const { value } = selectedOption;

        var parent = null;
        if (allAccounts && allAccounts.length) {
            parent = allAccounts.find(acc => acc.ConnectedAccounts.id == value);

            if (parent) {
                parent = parent.ConnectedAccounts.parent
            }
        }
        this.setState({ selectValue: selectedOption });
        onAccountChange(value, parent)
    }


    formatOption = (lists) => {
        const formatedList = lists.map((item) => ({ label: item.ConnectedAccounts.value2, value: item.ConnectedAccounts.id }));
        if (formatedList && formatedList.length > 0) {
            return [...formatedList, { label: 'other', value: 0 }];
        }
        return []
    }

    render() {
        const { tab, connectedAcc, text, onTextChange, onAlbumFieldChange, errorMsg, albumUrl, albumUrlError, onAddAccount, album, feedIcon, onCheckHandler, networkId, onAlbumChange, connetedAccount, onConnectedAccountChange, authenticate, onAuthenticateChange, error } = this.props
        const { handles, showHandle, formatedList, allAccounts, selectValue } = this.state;

        return (
            <>
                {tab == 'Page' || tab == "Albums" ?
                    <div className="row">
                        {tab == 'Albums' ?
                            <div className="col-sm-4">
                                <div className="mb-3">
                                    <Select className="selectpicker selectpicker-lg" options={[{ label: 'Single Album', value: '65' }, { label: 'All Facebook Page Albums', value: '67' }]} classNamePrefix="selectpicker" defaultValue={album} onChange={(e) => onAlbumChange(e)} />
                                </div>
                            </div> : null}
                        <div className={tab == "Albums" ? "col-sm-8" : "col-sm-12"}>
                            <div className="position-relative w-100 mb-3">
                                <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                    {feedIcon(8)}
                                </span>
                                <input autoComplete="off" data-limit="100" placeholder={`Enter ${tab == "Page"?"Page":'Album'} URL`} value={tab == "Page" ? text : albumUrl} className={`form-control form-control-lg fw-bolder ps-9${(tab == "Page" && error) || (tab == "Albums" && albumUrlError) ? ' is-invalid' : ''}`} id={`${tab}-text`} onChange={(e) => tab == "Page" ? this.handleTextChange(e.target.value) : onAlbumFieldChange(e.target.value)} type="text" />
                                <div className="handlelist" id="" style={showHandle ? { display: "block" } : { display: "none" }}>
                                    {handles && handles.length > 0 ? <Dropdown className={`w-100`} show>
                                        <Dropdown.Menu className="w-100">
                                            <PerfectScrollbar options={{ wheelPropagation: false }}>
                                                <div className="feedDropdown">
                                                    {handles.map(item => {
                                                        return (<Dropdown.Item className="text-break flex-column justify-content-start align-items-start" key={item.id} onClick={() => this.onSelectHandle(item)} style={{ whiteSpace: 'normal' }}>
                                                            <p className="mb-1 fs-9 fw-bold">{item.name}</p>
                                                            <span className="fs-10">{item.link}</span>
                                                        </Dropdown.Item>);
                                                    })}
                                                </div>
                                            </PerfectScrollbar>
                                        </Dropdown.Menu>
                                    </Dropdown> : null}
                                </div>
                                {(tab == "Page" && error) || (tab == "Albums" && albumUrlError) ? <span className="invalid-feedback">{errorMsg}</span> : null}
                            </div>
                        </div>
                    </div>
                    :
                    tab == "My Profile Posts"
                        ?
                        !(connectedAcc && connectedAcc.length > 0) ? <div className="otherSection py-2 mb-2">
                            <button type="button" className="btn btn-outline btn-lg align-items-center d-flex signInWithBrand m-auto" onClick={onAddAccount}>
                                <span className="h-40px w-40px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-light">
                                    <img src={baseUrl("media/icons/social-icons/facebook.svg")} height={43} width={43} alt="facebook" />
                                </span>
                                <div className="overflow-hidden signIn_txt px-5">
                                    <span className="text-gray-800 mb-0 fs-8 fw-medium text-ellipsis me-2">Connect Facebook</span>
                                </div>
                            </button>
                        </div> : null
                        :
                        connectedAcc && connectedAcc.length > 0 && allAccounts && allAccounts.length > 0 ?
                            <div className="mb-3">
                                <Select className="selectpicker selectpicker-lg" options={formatedList} classNamePrefix="selectpicker" value={selectValue} onChange={(e) => this.onAccChange(e)} />
                            </div>
                            :
                            <div className="otherSection py-2 mb-2">
                                <button type="button" className="btn btn-outline btn-lg align-items-center d-flex signInWithBrand m-auto" onClick={onAddAccount}>
                                    <span className="h-40px w-40px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-light">
                                        <img src={baseUrl("media/icons/social-icons/facebook.svg")} height={43} width={43} alt="facebook" />
                                    </span>
                                    <div className="overflow-hidden signIn_txt px-5">
                                        <span className="text-gray-800 mb-0 fs-8 fw-medium text-ellipsis me-2">Connect Facebook</span>
                                    </div>
                                </button>
                            </div>
                }
                {/* <div className="hideforBSearch">
                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                        <CheckBoxField isChecked={moderation} onChangeHandler={onCheckHandler} name={"moderation"}>
                            <span className="form-check-label">Manually approve posts before making them public?</span>
                        </CheckBoxField>
                    </label>
                </div>  */}
                {(connectedAcc && connectedAcc.length > 0) ? <div style={{ display: !["Page Reviews", "Page Mentions"].includes(tab) ? 'block' : "none" }}><AccountPicker connectedAcc={connectedAcc} networkId={networkId} onConnectedAccountChange={onConnectedAccountChange} /></div> : null}
                <ShowHideOption onChangeHandler={this.props.onCheckHandler}/>
            </>
        )
    }
}

export default FacebookTab;

