import React, { Component } from 'react'
import Uppy from '@uppy/core';
import { DragDrop } from '@uppy/react'
import Transloadit from '@uppy/transloadit';
import { toast } from 'react-toastify';
import { MEDIA_PATH } from '../../../../../Services/Api';



const uppyKey = '0701d3d7f8d94e92a65a9cba388b1d96';
const uppyBucket = 'cloud.taggbox.com';
const uppyAwsKey = '47ABVORKG52UYE1KB28S';
const uppyAwsSecret = 'pcaTfeokaz28geAAns08H3yaGbOVz6ILgTptu19v';


class BulkDropzone extends Component {
    constructor(props) {
        super(props);

        this.uppyOne = new Uppy({
            debug: true, autoProceed: true, restrictions: {
                maxFileSize: 5000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['.csv'],
            }
        }).use(Transloadit, {
            target: '#a',
            params: {
                auth: {
                    key: uppyKey
                },
                steps: {
                    store: {
                        robot: "/wasabi/store",
                        bucket_region: "us-west-1",
                        bucket: uppyBucket,
                        key: uppyAwsKey,
                        secret: uppyAwsSecret,
                        path: 'postimage/${file.id}'
                    },
                }
            },
            waitForEncoding: true
        });
    }


    componentDidMount() {
        this.uppyOne.on('transloadit:result', (stepName, result) => {
            this.props.onUploadSuccess(result.original_name, result.url);
            this.uppyOne.cancelAll()
        });
        this.uppyOne.on("restriction-failed", (file, error) => {

            let fileType = file.type.split('/');

            if (fileType[0] != 'csv') {
                toast.error("You can only upload csv File",{autoClose:3000});
                return false
            }


            if (file.size > 5000000) {
                toast.error("Maximum allowed size of 4.8 MB",{autoClose:3000});
                return false
            }

        });
    }

    componentWillUnmount() {
        this.uppyOne.cancelAll()
        this.uppyOne.close({ reason: 'unmount' })
    }

    render() {
        const { fileName } = this.props;

        return <div className="bulkURL-box mb-2">
            <DragDrop
                proudlyDisplayPoweredByUppy={false}
                hideUploadButton={false}
                showProgressDetails={false}
                disableThumbnailGenerator={false}
                height={220}
                width={'100%'}
                showSelectedFiles={true}
                showRemoveButtonAfterComplete={false}
                disableInformer={true}
                uppy={this.uppyOne}
            />
            <div className="d-flex align-items-center mb-3 fw-medium">
                <span className='text-muted me-1'>File Name :</span>{fileName}
            </div>
        </div>

    }
}


export default BulkDropzone