import React, { Component } from 'react'
import SearchPlace from '../../Elements/SearchPlace';
import CheckBoxField from '../../Elements/CheckboxField'
import InputField from '../../Elements/InputField';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Dropdown } from 'react-bootstrap';
import { youtubeAutoComplete } from '../../../../../Services/Actions/addFeedAction';
import { debounce } from '../../../../../Services/Utils/utils';


export default class YoutubeTabs extends Component {

    state = {
        list: [],
        spinnerActive: false
    }

    autoComplete = debounce((query, type) => youtubeAutoComplete(query, type).then(res => {
        const { data } = res
        if (data && Object.keys(data).length > 0) {
            this.setState({ list: data.items })
        }
    }))


    onYoutubeChange = query => {
        const { onValueChange, tab } = this.props
        this.setState({ spinnerActive: true })
        if (["Play List", "Channel Url", "Shorts"].includes(tab)) {
            try {
                const type = tab == "Play List" ? "playlist" : "channel"
                onValueChange(query);
                this.autoComplete(query, type);
                this.setState({ spinnerActive: false })
            } catch (error) {

                this.setState({ spinnerActive: false })
            }
        }
        else {
            onValueChange(query);
            this.setState({ spinnerActive: false })
        }
    }

    onListClick = (item) => {
        try {
            const { onValueChange, tab, onChannelIdChange, } = this.props
            onValueChange(tab == "Play List" ? item.snippet.title : item.snippet.channelTitle)
            onChannelIdChange(`${tab == "Play List" ? item.id.playlistId : tab == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`)
            document.getElementById(`feedsfield-${tab}`).value = `https://www.youtube.com/${tab == "Play List" ? "playlist?list=" : "channel/"}${tab == "Play List" ? item.id.playlistId : tab == "Shorts" ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`
            this.setState({ list: [] })
        } catch (error) {

        }
    }

    render() {
        const { tab, onValueChange, checkBox, onCheckBoxChange, onKeywordChange, keyword, error, onLatlanChange, shortError, playListError, keywordError, locationError, locationKeywordError, filter, feedIcon } = this.props
        const { list, spinnerActive } = this.state

        return (
            <>
                <div className="row">
                    <div className={`col-md-${tab == "Location" ? '6' : '12'}`}>
                        <div className="mb-3">
                            {tab == "Location" ?
                                <SearchPlace onFieldTextChange={onValueChange} onLatlanChange={onLatlanChange} error={(tab == "Location" && locationError)} />
                                :
                                <Dropdown className={`w-100 ${(tab == "Channel Url" && error) || (tab == "Shorts" && shortError) || (tab == "Play List" && playListError) || (tab == "Keywords" && keywordError) || (tab == "Location" && locationError) ? 'is-invalid' : ''}`} autoClose="outside">
                                    <Dropdown.Toggle variant='link' className="p-0  text-decoration-none w-100 arrow_disabled position-relative">
                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                            {feedIcon(filter)}
                                        </span>
                                        <input placeholder={`${tab == "Location" ? "" : "Enter"} ${tab == "Shorts" || tab == "Channel Url" ? "channel url" : tab == "Play List" ? "Play List ID" : tab == "Keywords" ? "keyword" : ""}`} className={`form-control form-control-lg fw-bolder px-9`} id={`feedsfield-${tab}`} type="text" onChange={e => this.onYoutubeChange(e.target.value)} />
                                        {spinnerActive ? <div className="spinner" style={{ top: -22 }}></div> : null}
                                    </Dropdown.Toggle>
                                    {list && list.length ?
                                        <Dropdown.Menu className="w-100">
                                            <PerfectScrollbar options={{ wheelPropagation: false }}>
                                                <div className="feedDropdown">
                                                    {list.map(item => (
                                                        <Dropdown.Item onClick={() => this.onListClick(item)}>
                                                            <div className="d-flex algin-items-center">
                                                                <img className="h-30px w-30px object-center-cover rounded-1 me-1" src={item.snippet.thumbnails.default.url} />
                                                                <div className="d-flex flex-column justify-content-start">
                                                                    <p className="fw-bold fs-8 mb-0">{item.snippet.channelTitle}</p>
                                                                    <span className="fs-10 mb-0">{`https://www.youtube.com/${tab == "Play List" ? "playlist?list=" : "channel/"}${tab == "Play List" ? item.id.playlistId : item.id.channelId}`}</span>
                                                                </div>
                                                            </div>
                                                        </Dropdown.Item>
                                                    ))}
                                                </div>
                                            </PerfectScrollbar>
                                        </Dropdown.Menu>
                                        : null}
                                </Dropdown>
                            }
                            {(tab == "Channel Url" && error) || (tab == "Shorts" && shortError) || (tab == "Play List" && playListError) || (tab == "Keywords" && keywordError) || (tab == "Location" && locationError) ? <span className="invalid-feedback">This field is required</span> : null}
                        </div>
                    </div>
                    {tab == "Location" ?
                        <div className="col-md-6">
                            <div className="position-relative mb-3">
                                <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                    {feedIcon(4)}
                                </span>
                                <InputField name={"keyword"} className={`form-control form-control-lg fw-bold px-9 ${(tab == "Location" && locationKeywordError) ? 'is-invalid' : ''}`} placeholder="Enter keywords" value={keyword} onChangeHandler={(e) => onKeywordChange(e.target.value)} />
                            </div>
                            {(tab == "Location" && locationKeywordError) ? <span className="invalid-feedback">This field is required</span> : null}
                        </div>
                        : null
                    }

                </div>
            </>
        )
    }
}
