import React, { Component } from 'react';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter, showAddAccountAlert } from '../../../../Services/Utils/utils';
import InputField from '../Elements/InputField';
import CheckBoxField from '../Elements/CheckboxField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { addUserAccount, createFeedAction } from '../../../../Services/Actions/addFeedAction';
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import { connect } from 'react-redux';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import AccountPicker from '../Elements/AccountPicker';
import AccountSelect from './components/AccountSelect';
import InfoSection from '../Elements/InfoSection';
import ShowHideOption from '../Elements/ShowHideOption';

const channel = new BroadcastChannel('connect');

class TiktokFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hashtag: "",
            handle: '',
            hashtagPost: false,
            postUrl: "",
            moderation: false,
            connetedAccount: null,
            error: false,
            loading: false,
            selectedChildAccount: {},
            personalAcc: {},
            account: 0,
            isAuthRequired: props.isAuthRequired,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype,
            handleError: false,
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, topicError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onConnectedAccountChange = (value) => this.setState({ account: value })

    addTiktokAccount = () => {
        const { networkData } = this.props;
        showAddAccountAlert(networkData);
    }
    onSelectedChildAccountChange = (value) => this.setState({ selectedChildAccount: value })

    onPersonalAccChange = (value) => this.setState({ personalAcc: value })
    createFeed = (connectAccountId) => {
        const { postUrl, moderation, selectedFilterId, account, selectedChildAccount, personalAcc, hashtag, handle, hashtagPost } = this.state;
        const { collection, networkId } = this.props;
        var collectionId = []
        if (collection && collection.length > 0) {
            collection.map((iCollection) => {
                collectionId.push(iCollection.value)
            })
        }
        const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
        var obj = {
            ...isCollection,
            networkId: networkId,
            hashtagCaption: hashtagPost ? 1 : 0,
            feedText: selectedFilterId == 72 ? hashtag : handle,
            filter: selectedFilterId,
            moderation: moderation ? 1 : 0,

        }
        if (selectedFilterId == 71) {
            obj['accountId'] = connectAccountId ? connectAccountId : account ? account : ""
        }
        else obj['accountId'] = ""

        this.setState({ error: false, topicError: false, loading: true })

        createFeedAction(obj, networkId, (getFeedId) => {
            this.props.getAllfeeds()
            this.setState({ loading: false });
            this.props.closeAddFeed();
            if (getFeedId) {
                if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                else {
                    if (String(window.location.href).includes("assets")) {
                        channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                    }
                    setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                }


            }
        });
    }

    onSubmit = () => {
        // const { postUrl, selectedFilterTab, account } = this.state;

        // if (selectedFilterTab == 'Personal Account' && (!account || account == 0)) {
        //     this.addTiktokAccount();
        // } else if (selectedFilterTab == 'Post Url' && postUrl == '') {
        //     this.setState({ error: true });
        // } else {
        //     this.createFeed();
        // }



        const { hashtag, handle, selectedFilterId, isAuthRequired } = this.state;
        const { accountId } = this.props;
        if ((!hashtag || hashtag == "") && selectedFilterId == 72) {
            this.setState({ error: true, loading: false })
        } else if ((!handle || handle == "") && selectedFilterId == 71) {
            this.setState({ handleError: true, isLoading: false })
        } else {
            if ([72, 71].includes(selectedFilterId)) {
                this.createFeed(accountId != null ? accountId : 0);
            } else this.createFeed(null)
        }
    }

    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId } = response;

        addUserAccount(networkId, data, (connectAccountId) => this.createFeed(connectAccountId));

    }

    componentDidMount() {
        this.getAccounts();
        this.getPersonalAccounts();
        channel.addEventListener("message", this.readAuthenticationCode);
    }
    getPersonalAccounts = () => {
        const { personalAccount, onPersonalAccChange } = this.props;
        if (personalAccount && personalAccount.length > 0) {
            const formatedOption = this.formatOption(personalAccount, true);
            if (onPersonalAccChange) onPersonalAccChange(formatedOption[0]);
            this.setState({ personalAccountList: formatedOption, allPersonalAcc: personalAccount, selectPersonal: formatedOption[0] });
        }
    }
    formatOption = (lists, isPersonal) => {
        const formatedList = lists.map((item) => ({ label: isPersonal ? item.ConnectedAccounts.accountName : item.ConnectedAccounts.value2, value: item.ConnectedAccounts.id }));
        if (formatedList && formatedList.length > 0) {
            return [...formatedList, { label: 'other', value: 0 }];
        }
        return []
    }
    componentWillUnmount() {
        channel.removeEventListener("message", this.readAuthenticationCode);
    }

    feedIcon = (iconId) => {
        switch (iconId) {
            case 72:
            case 26: // Hashtag
                return <i className="ri-hashtag icon-6"></i>
                break;
            case 71:
            case 23:
            case 24: // Handle
                return <i className="ri-at-line icon-6"></i>
                break;
            case 1: // Account
                return <i className="ri-account-circle-line icon-6"></i>
                break;
            case 70: // Link
                return <i className="ri-link icon-6"></i>
                break;
            default:
                break;
        }
    }

    getInfo = (tab) => {
        switch (tab) {
            case 'hashtag':
                return 'TikTok only allows posts from the last 24 hours. If there are no recent posts in the 24-hour duration, then the top 50 posts will be aggregated. Also, all your upcoming posts will be aggregated. Posts collected via hashtags will not include any information of the user.';
            case 'handle':
                return 'Initially 32 posts will be aggregated through handle after which all the upcoming posts will be aggregated.';
            default:
                break;
        }
    }

    getAccounts = () => {
        const { connectedAcc } = this.props
        var allAccounts = [];
        if (connectedAcc && connectedAcc.length > 0) {
            connectedAcc && connectedAcc.length && connectedAcc.map(acc => {
                if (acc.ConnectedAccounts.child && acc.ConnectedAccounts.child.length > 0) {
                    allAccounts = [...allAccounts, ...acc.ConnectedAccounts.child];
                }
                else {
                    this.setState({ allAccounts: [] })
                }
            })

            if (allAccounts && allAccounts.length > 0) {
                const formatedOption = this.formatOption(allAccounts, false);
                this.onSelectedChildAccountChange(formatedOption[0]);
                this.setState({ allAccounts, formatedList: formatedOption, allConnectAcc: connectedAcc, selectValue: formatedOption[0] });
            }
        } else {
            this.onSelectedChildAccountChange(null)
            this.setState({ allAccounts: [] })
        }
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, urlError: false });
    }
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })
    render() {
        const { filterTabs, connectedAccountList, networkId, personalAccount } = this.props;
        const { postUrl, error, loading, selectedFilterTab, handleError, hashtag, handle, hashtagPost, connetedAccount } = this.state;

        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                        {
                                            filterTabs && filterTabs.map((filterTab, index) => {
                                                const { feedName, feedtype, filter } = filterTab;
                                                return <Nav.Item className="p-1" key={index}>
                                                    <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                        <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                            {this.feedIcon(filter)}
                                                        </span>
                                                        {capitalizeFirstLetter(feedtype)}
                                                    </Nav.Link>
                                                </Nav.Item>
                                            })
                                        }
                                    </Nav>
                                </div>
                            </div>
                        </div>
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">
                                        <Tab.Content>
                                            {(filterTabs && filterTabs.length > 0) ? filterTabs.map(filter => <Tab.Pane eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0">
                                                {
                                                    selectedFilterTab == 'hashtag' || selectedFilterTab == "handle" ?
                                                        <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                            <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                                {this.feedIcon(selectedFilterTab == "hashtag" ? 72 : 71)}
                                                            </span>
                                                            <InputField name={selectedFilterTab} className={`form-control form-control-lg fw-bold px-9 ${(selectedFilterTab == "hashtag" && error) || (selectedFilterTab == "handle" && handleError) ? 'is-invalid' : ''}`} placeholder={`Enter user ${selectedFilterTab}`} value={selectedFilterTab == "hashtag" ? hashtag : handle} onChangeHandler={this.onChangeHandler} />
                                                            {(selectedFilterTab == "hashtag" && error) || (selectedFilterTab == "handle" && handleError) ? <span className="invalid-feedback">This field is required</span> : null}
                                                        </div> : null
                                                }
                                                {
                                                    selectedFilterTab == 'hashtag' ? <div className="hideforBSearch">
                                                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                            <CheckBoxField isChecked={hashtagPost} onChangeHandler={this.onCheckHandler} name={"hashtagPost"}>
                                                                <span className="form-check-label">Get posts containing hashtag only in caption.</span>
                                                            </CheckBoxField>
                                                        </label>
                                                    </div> : null
                                                }
                                                <ShowHideOption onChangeHandler={this.onCheckHandler} />
                                                {(connectedAccountList && connectedAccountList.length > 0) ? <div style={{ display: ["hashtag", "handle"].includes(selectedFilterTab) ? 'block' : "none" }}> <AccountPicker connectedAcc={connectedAccountList} personalAccount={personalAccount} personal={selectedFilterTab == 'personal'} networkId={networkId} onConnectedAccountChange={this.onConnectedAccountChange} /> </div> : null}
                                                <InfoSection text={this.getInfo(selectedFilterTab)} />
                                            </Tab.Pane>
                                            ) : null}
                                            <Tab.Pane className="nav-link className= p-0">
                                                {connectedAccountList && connectedAccountList.length ? <AccountSelect connectedAcc={connectedAccountList} onConnectedAccountChange={this.onConnectedAccountChange} /> :
                                                    <div className="py-2">
                                                        <button type="button" className="btn btn-outline btn-lg align-items-center d-flex signInWithBrand m-auto" onClick={this.addTiktokAccount}>
                                                            <span className="h-40px w-40px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-light">
                                                                <img src={baseUrl("media/icons/social-icons/tiktok.svg")} height={43} width={43} alt="facebook" />
                                                            </span>
                                                            <div className="overflow-hidden signIn_txt px-4">
                                                                <span className="text-gray-800 mb-0 fs-8 fw-bold text-ellipsis me-2">Connect TikTok</span>
                                                            </div>
                                                        </button>
                                                    </div>}
                                            </Tab.Pane>
                                            <Tab.Pane eventKey={"Post Url"}>
                                                <div className="mb-2 mb-xl-3">
                                                    <div className="position-relative w-100">
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(70)}
                                                        </span>
                                                        <InputField name={"postUrl"} className={`form-control form-control-lg fw-bold px-9 ${error ? 'is-invalid' : ''}`} placeholder="Enter Tiktok Post Url" value={postUrl} onChangeHandler={this.onChangeHandler} />
                                                        {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                            </Tab.Pane>
                                        </Tab.Content>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount,
        connectedAccountList: state.addFeed.connectedAcc,
        personalAccount: state.addFeed.personalAccount,
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TiktokFeed)
