import { Component } from "react";

class InfoSection extends Component {
    render() {
        const { text } = this.props;
        return <div className="d-flex alert alert-info">
                    <i className="ri-information-line icon-3 me-2 text-muted"></i>
                    <div className="d-flex justify-content-center align-items-start flex-column flex-grow-1">
                        <p className="mb-0">{text}</p>
                    </div>
                </div>
    }
}

export default InfoSection;