import { Component } from "react";
import { connect } from 'react-redux';
import { Nav, Tab } from 'react-bootstrap';
import { VALIDATE_URL, capitalizeFirstLetter, showAddAccountAlert } from "../../../../Services/Utils/utils";
import { addUserAccount, createFeedAction, selectAccount } from "../../../../Services/Actions/addFeedAction";
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import FacebookTab from "./components/FacebookTab";
import SubmitFeedRequest from "../Elements/SubmitFeedRequest";
import { toast } from "react-toastify";
import ConnectAccount from "../Elements/ConnectAccount";


const channel = new BroadcastChannel('connect');
class FacebookFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            textField: "",
            account: null,
            albumUrl: '',
            errorMsg: 'This field is required',
            error: false,
            albumUrlError: false,
            album: { label: 'Single Album', value: '65' },
            connetedAccount: null,
            accountList: [],
            feedObj: {},
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype,
            moderation: false
        }
    }


    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }

    onTextFieldChange = value => this.setState({ textField: value, error: false })
    onAlbumFieldChange = value => this.setState({ albumUrl: value, albumUrlError: false })
    onAccountChange = (value, parent) => {
        if (["Page Reviews", "Page Mentions"].includes(this.state.selectedFilterTab)) {
            if (value == 0) this.props.selectAccount(null)
            else this.props.selectAccount(value)
        }

        this.setState({ connetedAccount: parent })
        this.setState({ account: value });
    }


    onCheckHandler = event => { this.setState({ [event.target.name]: event.target.checked }) }
    onAlbumChange = value => this.setState({ album: value })
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })

    addFacebookAccount = () => {
        const { networkData } = this.props;
        this.setState({ feedObj: this.createFeedPostData() });
        showAddAccountAlert(networkData);
    }


    createFeedPostData = () => {
        const { textField, album, connetedAccount, account, albumUrl, moderation, selectedFilterId } = this.state;
        const { collection, networkId } = this.props;
        var collectionId = []
        if (collection && collection.length > 0) {
            collection.map((iCollection) => {
                collectionId.push(iCollection.value)

            })

        }
        const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
        var obj = {
            ...isCollection,
            networkId: networkId,
            filter: selectedFilterId == 67 ? parseInt(album.value) : selectedFilterId,
            connetedAccount: [62, 55].includes(selectedFilterId) ? connetedAccount : 0,
            account: [62, 55].includes(selectedFilterId) ? account : connetedAccount,
            field: textField,
            onlyReels: 0,
            hashtagIGTV: 0,
            moderation: moderation ? 1 : 0,
        }
        if (selectedFilterId == 67) {
            obj['field'] = albumUrl;
        }
        if (![8, 67].includes(selectedFilterId)) {
            obj['field'] = '';
        }

        return obj;
    }


    createFeed = () => {

        let obj = this.createFeedPostData();
        this.setState({ loading: true, error: false, handleError: false, mentionError: false, advanceError: false, favoriteError: false, listNameError: false, showListError: false });
        createFeedAction(obj, this.props.networkId, (getFeedId) => {
            this.props.getAllfeeds()
            this.setState({ loading: false });
            this.props.closeAddFeed();
            if (getFeedId) {
                if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                else {
                    if (String(window.location.href).includes("assets")) {
                        channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                    }
                    setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                }


            }
        })
    }

    onSubmit = () => {
        const { textField, selectedFilterId, connetedAccount, albumUrl } = this.state;
        const { accountId } = this.props;

        if ((!textField || textField == "") && selectedFilterId == 8) {
            this.setState({ error: true, loading: false, errorMsg: 'This field is required' })
        } else if (selectedFilterId == 8 && !VALIDATE_URL(textField)) {
            this.setState({ error: true, loading: false, errorMsg: 'Please enter valid Page Url' })
        } else if ((!albumUrl || albumUrl == "") && selectedFilterId == 67) {
            this.setState({ albumUrlError: true, loading: false, errorMsg: 'This field is required' })
        } else if (selectedFilterId == 67 && !VALIDATE_URL(albumUrl)) {
            this.setState({ albumUrlError: true, loading: false, errorMsg: 'Please enter valid Album Url' })
        } else {

            if (connetedAccount && accountId) {
                this.createFeed();
            } else {
                this.addFacebookAccount();
            }
        }
    }


    createFeedFromPage = (accId, parentId, name) => {
        let { feedObj } = this.state;
        const { networkId, history } = this.props;

        if (networkId == 3) {
            feedObj = { ...feedObj, account: accId, connetedAccount: parentId }
        }

        this.setState({ loading: true, error: false, handleError: false, mentionError: false, advanceError: false, favoriteError: false, listNameError: false, showListError: false });
        createFeedAction(feedObj, networkId, (getFeedId) => {
            this.setState({ loading: false });
            this.props.closeAddFeed();
            if (getFeedId) {
                if (feedObj && Object.keys(feedObj).length > 0 && feedObj.collection && feedObj.collection != "" && String(feedObj.collection).split(",").length == 1) this.props.history(`/assets/${feedObj.collection}`);
                else {
                    if (String(window.location.href).includes("assets")) {
                        channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                    }
                    setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                }


            }


        }
        );
    }

    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId } = response;
        const { selectedFilterId } = this.state;
        //55, 62 filter id 
        addUserAccount(networkId, data, [55, 62].includes(selectedFilterId) ? (responseData) => this.setState({ accountList: responseData }) : (connectAccountId) => this.createFeed(connectAccountId), selectedFilterId);

    }

    componentDidMount() {
        channel.addEventListener("message", this.readAuthenticationCode);
    }

    componentWillUnmount() {
        channel.removeEventListener("message", this.readAuthenticationCode);
    }
    feedIcon = (iconId) => {
        switch (iconId) {
            case 8: // Page
                return <i className="ri-flag-2-line icon-6"></i>
                break;
            case 67: // Gallery
                return <i className="ri-gallery-line icon-6"></i>
                break;
            case 55: // Review
                return <i className="ri-star-line icon-6"></i>
                break;
            case 62: // Handle
                return <i className="ri-at-line icon-6"></i>
                break;
            case 1: // Account
                return <i className="ri-account-circle-line icon-6"></i>
                break;
            default:
                break;
        }
    }
    render() {
        const { error, selectedFilterTab, albumUrl, albumUrlError, errorMsg, moderation, loading, connetedAccount, album, textField, account, accountList, selectedFilterId } = this.state;
        const { filterTabs, networkId, connectedAccountList } = this.props;

        return <>{
            !(accountList && accountList.length > 0) ? <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">

                                        <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                            {
                                                filterTabs && filterTabs.map((filterTab, index) => {
                                                    const { feedName, feedtype, filter } = filterTab;
                                                    return <Nav.Item className="p-1" key={index}>
                                                        <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                            <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                                {this.feedIcon(filter)}
                                                            </span>
                                                            {capitalizeFirstLetter(feedtype === 'favorites' ? feedName : feedtype)}
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                })
                                            }
                                        </Nav>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">
                                        <Tab.Content>
                                            {(filterTabs && filterTabs.length > 0) ? filterTabs.map(filter => (
                                                <Tab.Pane eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0">
                                                    <FacebookTab
                                                        onAddAccount={this.addFacebookAccount}
                                                        connetedAccount={connetedAccount}
                                                        onConnectedAccountChange={this.onConnectedAccountChange}
                                                        tab={selectedFilterTab}
                                                        text={textField}
                                                        error={error}
                                                        errorMsg={errorMsg}
                                                        connectedAcc={connectedAccountList}
                                                        onTextChange={this.onTextFieldChange}
                                                        account={account}
                                                        onAccountChange={this.onAccountChange}
                                                        onCheckHandler={this.onCheckHandler}
                                                        onAlbumChange={this.onAlbumChange}
                                                        album={album}
                                                        moderation={moderation}
                                                        albumUrl={albumUrl}
                                                        albumUrlError={albumUrlError}
                                                        onAlbumFieldChange={this.onAlbumFieldChange}
                                                        feedIcon={this.feedIcon}
                                                    />
                                                </Tab.Pane>
                                            )) : null}
                                        </Tab.Content>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </> :
                <ConnectAccount accountList={accountList} loading={loading} networkId={networkId} connectedAcc={connectedAccountList} filterId={selectedFilterId} createFeedFromPage={this.createFeedFromPage} onBack={() => this.setState({ accountList: [] })} />
        }
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount,
        connectedAccountList: state.addFeed.connectedAcc
    }
}

const mapDispatchToProps = dispatch => {
    return {
        selectAccount: (id) => dispatch(selectAccount(id)),
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(FacebookFeed)