import { Component } from "react";
import { connect } from 'react-redux';
import { Nav, Tab } from 'react-bootstrap';
import { capitalizeFirstLetter, showAddAccountAlert } from "../../../../Services/Utils/utils";
import { addUserAccount, createFeedAction } from "../../../../Services/Actions/addFeedAction";
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import TwitterTab from "./components/TwitterTab";
import SubmitFeedRequest from "../Elements/SubmitFeedRequest";


const channel = new BroadcastChannel('connect');

const FeedIcon = ({ iconId }) => {
    switch (iconId) {
        case 2: // Hashtag
            return <i className="ri-hashtag icon-6"></i>
            break;
        case 1:
        case 7: // Handle
            return <i className="ri-at-line icon-6"></i>
            break;
        case 3: // List
            return <i className="ri-list-check icon-6"></i>
            break;
        case 5: // Advance
            return <i className="ri-search-line icon-6"></i>
            break;
        case 9: // Text
            return <i className="ri-text icon-6"></i>
            break;
        default:
            break;
    }
}
class Twitter extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hashtag: '',
            handle: '',
            mentions: '',
            advanced: '',
            userName: '',
            listName: '',
            favorites: '',
            userId: null,
            listId: null,
            loading: false,
            multiPhoto: true,
            excludeRetweets: true,
            excludeReplies: true,
            connectedAccount: null,
            moderation: false,
            error: false,
            handleError: false,
            mentionError: false,
            advanceError: false,
            favoriteError: false,
            showListError: false,
            listNameError: false,
            isAuthRequired: props.isAuthRequired,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, handleError: false, mentionError: false, advanceError: false, favoriteError: false, listNameError: false, showListError: false });
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }
    onCheckHandler = event => { this.setState({ [event.target.name]: event.target.checked }) }
    onListNameChange = (value) => this.setState({ userName: value, showListError: false })
    onListIdChange = (listId, userId) => this.setState({ listId: listId, userId: userId })
    onConnectedAccountChange = (value) => this.setState({ connetedAccount: value })

    addTwitterAccount = () => {
        const { networkData } = this.props;
        showAddAccountAlert(networkData);
    }


    createFeed = (connectAccountId) => {
        const { hashtag, handle, excludeRetweets, excludeReplies, multiPhoto, userId, listId, listName, userName, advanced, mentions, favorites, moderation, selectedFilterId, isAuthRequired } = this.state;
        const { collection, networkId, history } = this.props;
        var collectionId = []
        if (collection && collection.length > 0) {
            collection.map((iCollection) => {
                collectionId.push(iCollection.value)

            })

        }
        const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
        var obj = {
            ...isCollection,
            networkId: networkId,
            filter: selectedFilterId,
            moderation: moderation ? 1 : 0,
            excludeRetweet: excludeRetweets ? 1 : 0,
            excludeReplies: excludeReplies ? 1 : 0,
            multiplePhoto: multiPhoto ? 1 : 0,
            authRequire: isAuthRequired ? 1 : 0,
            accountId: connectAccountId
        }

        if (selectedFilterId == 2) {
            obj['feedText'] = hashtag
        } else if (selectedFilterId == 1) {
            obj['feedText'] = handle
        } else if (selectedFilterId == 7) {
            obj['feedText'] = mentions
        } else if (selectedFilterId == 4) {
            obj['feedText'] = favorites
        } else if (selectedFilterId == 5) {
            obj['feedText'] = advanced
        } else if (selectedFilterId == 3) {
            obj['feedText'] = userName;
            obj['listname'] = listName;
            obj['userId'] = userId;
            obj['listId'] = listId;
        }
        this.setState({ loading: true, error: false, handleError: false, mentionError: false, advanceError: false, favoriteError: false, listNameError: false, showListError: false });

        createFeedAction(obj, networkId, (getFeedId) => {
            this.props.getAllfeeds()
            this.setState({ loading: false, isAuthRequired: true });
            this.props.closeAddFeed();
            if (getFeedId) {
                if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                else {
                    if (String(window.location.href).includes("assets")) {
                        channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                    }
                    setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                }

            }
        });
    }

    onSubmit = () => {
        const { hashtag, advanced, handle, userName, listName, favorites, mentions, selectedFilterTab, isAuthRequired } = this.state;
        const { accountId } = this.props;


        if (selectedFilterTab == 'hashtag' && hashtag == '') {
            this.setState({ error: true });
        } else if (selectedFilterTab == 'handle' && handle == '') {
            this.setState({ handleError: true });
        } else if (selectedFilterTab == 'mentions' && mentions == '') {
            this.setState({ mentionError: true });
        } else if (selectedFilterTab == 'favorites' && favorites == '') {
            this.setState({ favoriteError: true });
        } else if (selectedFilterTab == 'advanced' && advanced == '') {
            this.setState({ advanceError: true });
        } else if (selectedFilterTab == 'list' && userName == '') {
            this.setState({ showListError: true });
        } else if (selectedFilterTab == 'list' && listName == '') {
            this.setState({ listNameError: true });
        } else {

            if (isAuthRequired) {
                if (accountId == null) {
                    this.addTwitterAccount();
                } else {
                    this.createFeed(accountId);
                }
            } else {
                this.createFeed(0);
            }
        }
    }

    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId } = response;

        addUserAccount(networkId, data, (connectAccountId) => this.createFeed(connectAccountId));

    }

    componentDidMount() {
        channel.addEventListener("message", this.readAuthenticationCode);
    }

    componentWillUnmount() {
        channel.removeEventListener("message", this.readAuthenticationCode);
    }

    render() {
        const { selectedFilterTab, loading } = this.state;
        const { filterTabs, networkId, connectedAccountList } = this.props;

        return <>
            <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                <div className="addSourceTabs">
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Select the type of posts</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <div className="">

                                    <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                        {
                                            filterTabs && filterTabs.map((filterTab, index) => {
                                                const { feedName, feedtype, filter } = filterTab;
                                                return <Nav.Item className="p-1" key={index}>
                                                    <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                        <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                            <FeedIcon iconId={filter} />
                                                        </span>
                                                        {capitalizeFirstLetter(feedtype === 'favorites' ? feedName : feedtype)}
                                                    </Nav.Link>
                                                </Nav.Item>
                                            })
                                        }
                                    </Nav>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="w-100">
                        <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                        <div className="card card-custom gutter-b">
                            <div className="card-body px-xxl-5">
                                <div className="">
                                    <Tab.Content>
                                        {(filterTabs && filterTabs.length > 0) ? filterTabs.map(filter => (
                                            <Tab.Pane eventKey={filter.feedtype} key={filter.filter} className="nav-link className= p-0">
                                                <TwitterTab
                                                    networkId={networkId}
                                                    tab={filter.feedtype}
                                                    filter={filter.filter}
                                                    {...this.state}
                                                    connectedAccountList={connectedAccountList}
                                                    onListIdChange={this.onListIdChange}
                                                    onConnectedAccountChange={this.onConnectedAccountChange}
                                                    onListNameChange={this.onListNameChange}
                                                    onExcRetweetChange={this.onCheckHandler}
                                                    onChangeHandler={this.onChangeHandler}
                                                    onCheckHandler={this.onCheckHandler}
                                                />
                                            </Tab.Pane>
                                        )) : null}
                                    </Tab.Content>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Tab.Container>
            <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount,
        connectedAccountList: state.addFeed.connectedAcc
    }
}
const mapDispatchToProps = dispatch => {
    return {
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Twitter)