import React, { Component } from 'react';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter, showAddAccountAlert } from '../../../../Services/Utils/utils';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { addUserAccount, createFeedAction, fetchConnectedAccounts } from '../../../../Services/Actions/addFeedAction';
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import { connect } from 'react-redux';
import SlackTab from './components/SlackTab';
import { toast } from 'react-toastify';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import ShowHideOption from '../Elements/ShowHideOption';


const channel = new BroadcastChannel('connect');

class SlackFeed extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            keyword: '',
            error: false,
            selectedAcc: null,
            moderation: false,
            channelError: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }

    handleSelect = (e) => {
        this.setState({ selectedAcc: e, channelError: false });
    }

    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }


    addSlackAccount = () => {
        const { networkData } = this.props;
        showAddAccountAlert(networkData);
    }


    createFeed = () => {
        const { selectedAcc, moderation, keyword, selectedFilterId } = this.state;
        const { collection, networkId, connectedAccountList } = this.props;
        const parentAcc = connectedAccountList && connectedAccountList.length > 0 ? connectedAccountList[0].ConnectedAccounts.id : '';
        var collectionId = []
        if (collection && collection.length > 0) {
            collection.map((iCollection) => {
                collectionId.push(iCollection.value)
            })
        }
        const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
        var obj = {
            ...isCollection,
            networkId: networkId,
            filter: selectedFilterId,
            moderation: moderation ? 1 : 0,
            connetedAccount: parentAcc,
            account: selectedAcc,
        }

        if (selectedFilterId == 2) {
            obj['slackkeyword'] = '';
        } else if (selectedFilterId == 1) {
            obj['slackkeyword'] = keyword
        }

        this.setState({ error: false, topicError: false, loading: true })
        createFeedAction(obj, networkId, (getFeedId) => {
            this.props.getAllfeeds()
            this.setState({ loading: false });
            this.props.closeAddFeed();
            if (getFeedId) {
                if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                else {
                    if (String(window.location.href).includes("assets")) {
                        channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                    }
                    setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                }


            }
        });
    }

    onSubmit = () => {
        const { keyword, selectedFilterTab, selectedAcc } = this.state;

        if (selectedFilterTab == 'Channel' && selectedAcc == null) {
            this.setState({ channelError: true });
        } else if (selectedFilterTab == 'Channel & Keyword' && selectedAcc == null) {
            toast.error('Need to connect slack account', { autoClose: 3000 });
        } else if (selectedFilterTab == 'Channel & Keyword' && keyword == '') {
            this.setState({ error: true });
        } else {
            this.createFeed();
        }
    }

    readAuthenticationCode = (event) => {
        const response = JSON.parse(event.data);
        const { data, networkId } = response;

        addUserAccount(networkId, data, () => this.props.getConnectedAccount(networkId));

    }

    componentDidMount() {
        channel.addEventListener("message", this.readAuthenticationCode);
    }

    componentWillUnmount() {
        channel.removeEventListener("message", this.readAuthenticationCode);
    }

    feedIcon = (iconId) => {
        switch (iconId) {
            case 2: // Link
                return <i className="ri-link icon-6"></i>
                break;
            case 1: // Link
                return <i className="ri-link icon-6"></i>
                break;
            default:
                break;
        }
    }
    render() {
        const { filterTabs, connectedAccountList } = this.props;
        const { keyword, selectedAcc, moderation, channelError, error, loading, selectedFilterTab } = this.state;
        const connectedAccounts = (connectedAccountList && connectedAccountList.length > 0) ? connectedAccountList[0].ConnectedAccounts : [];
        return (
            <>
                {!(connectedAccountList && connectedAccountList.length > 0) ?
                    <div className="addSourceTabs">
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Connect Your Slack Account</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="py-2">
                                        <button type="button" className="btn btn-outline btn-lg align-items-center d-flex signInWithBrand m-auto" onClick={() => this.addSlackAccount()}>
                                            <span className="h-40px w-40px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-light">
                                                <img src={baseUrl("media/icons/social-icons/slack.svg")} height={43} width={43} alt="facebook" />
                                            </span>
                                            <div className="overflow-hidden signIn_txt px-5">
                                                <span className="text-gray-800 mb-0 fs-9 fw-medium text-ellipsis me-2">Connect Slack</span>
                                            </div>
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    :
                    <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                        <div className="addSourceTabs">
                            <div className="w-100">
                                <h6 className="fs-7 mb-3">Select the type of posts</h6>
                                <div className="card card-custom gutter-b">
                                    <div className="card-body px-xxl-5">
                                        <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                            {
                                                filterTabs && filterTabs.map((filterTab, index) => {
                                                    const { feedName, feedtype, filter } = filterTab;
                                                    return <Nav.Item className="p-1" key={index}>
                                                        <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                            <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                                {this.feedIcon(filter)}
                                                            </span>
                                                            {capitalizeFirstLetter(feedtype)}
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                })
                                            }
                                        </Nav>
                                    </div>
                                </div>
                            </div>
                            <div className="w-100">
                                <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                                <div className="card card-custom gutter-b">
                                    <div className="card-body px-xxl-5">
                                        <div className="">
                                            <Tab.Content>
                                                <Tab.Pane eventKey={"Channel"}>
                                                    <SlackTab accounts={connectedAccounts.child} tab={'Channel'} moderation={moderation} onCheckHandler={this.onCheckHandler} error={error} keyword={keyword} handleInput={this.onChangeHandler} onSelect={this.handleSelect} selectAccount={selectedAcc} channelError={channelError} />
                                                </Tab.Pane>
                                                <Tab.Pane eventKey={"Channel & Keyword"}>
                                                    <SlackTab accounts={connectedAccounts.child} tab={'ChannelKeyword'} moderation={moderation} onCheckHandler={this.onCheckHandler} error={error} keyword={keyword} handleInput={this.onChangeHandler} onSelect={this.handleSelect} selectAccount={selectedAcc} channelError={channelError} />
                                                </Tab.Pane>
                                                <ShowHideOption onChangeHandler={this.onChangeHandler}/>
                                            </Tab.Content>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Tab.Container>
                }
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        accountId: state.addFeed.selectedAccount,
        connectedAccountList: state.addFeed.connectedAcc
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getConnectedAccount: (id) => dispatch(fetchConnectedAccounts(id)),
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SlackFeed)