import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Nav, Tab } from "react-bootstrap";
import { capitalizeFirstLetter } from '../../../../Services/Utils/utils';
import InputField from '../Elements/InputField';
import CheckBoxField from '../Elements/CheckboxField';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { createFeedAction } from '../../../../Services/Actions/addFeedAction';
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import ShowHideOption from '../Elements/ShowHideOption';


const channel = new BroadcastChannel('connect');
class Flickr extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hashtag: "",
            handle: "",
            moderation: false,
            error: false,
            handleError: false,
            loading: false,
            selectedFilterId: props.filterTabs[0].filter,
            selectedFilterTab: props.filterTabs[0].feedtype
        }
    }

    changeTab = (filterId, feedtype) => {
        this.setState({ selectedFilterId: filterId, selectedFilterTab: feedtype });
    }


    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, handleError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onManuallyApproveChange = (value) => this.setState({ moderation: value })

    onSubmit = () => {
        const { hashtag, handle, moderation, selectedFilterTab, selectedFilterId } = this.state;
        const { collection, networkId, history } = this.props;

        if (selectedFilterTab == 'Hashtag' && hashtag == '') {
            this.setState({ error: true });
        } else if (selectedFilterTab == 'Handle' && handle == '') {
            this.setState({ handleError: true });
        }
        else {

            this.setState({ error: false, handleError: false, loading: true })
            var collectionId = []
            if (collection && collection.length > 0) {
                collection.map((iCollection) => {
                    collectionId.push(iCollection.value)

                })

            }
            const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }

            var obj = {
                ...isCollection,
                networkId: networkId,
                field1: handle,
                field2: hashtag,
                filter: selectedFilterId,
                moderation: moderation ? 1 : 0
            }

            if (selectedFilterId == 1) {
                obj['field2'] = ''
            } else if (selectedFilterId == 2) {
                obj['field1'] = ''
            }

            createFeedAction(obj, networkId, (getFeedId) => {
                this.props.getAllfeeds()
                this.setState({ loading: false });
                this.props.closeAddFeed();
                if (getFeedId) {
                    if (obj && Object.keys(obj).length > 0 && obj.collection && obj.collection != "" && String(obj.collection).split(",").length == 1) this.props.history(`/assets/${obj.collection}`);
                    else {
                        if (String(window.location.href).includes("assets")) {
                            channel.postMessage(JSON.stringify({ type: 'assets-redirect' }));
                        }
                        setTimeout(() => this.props.history(`/assets?feedId=${getFeedId}`), 300)
                    }
    
                }
            });
        }
    }
    feedIcon = (iconId) => {
        switch (iconId) {
            case 1: // @
                return <i className="ri-at-line icon-6"></i>
                break;
            case 2: // Hashtag
                return <i className="ri-hashtag icon-6"></i>
                break;
            default:
                break;
        }
    }
    render() {
        const { filterTabs } = this.props;
        const { hashtag, handle, moderation, handleError, error, loading, selectedFilterTab } = this.state
        return (
            <>
                <Tab.Container id="left-tabs-example" defaultActiveKey={selectedFilterTab}>
                    <div className="addSourceTabs">
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Select the type of posts</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">

                                        <Nav variant="pills" className="flex-wrap justify-content-center pills-lg">
                                            {
                                                filterTabs && filterTabs.map((filterTab, index) => {
                                                    const { feedName, feedtype, filter } = filterTab;
                                                    return <Nav.Item className="p-1" key={index}>
                                                        <Nav.Link eventKey={feedtype} onClick={() => this.changeTab(filter, feedtype)} key={`networkTab${filter}`}>
                                                            <span className="svg-icon" id={`svg-icon-${filter}`}>
                                                                {this.feedIcon(filter)}
                                                            </span>
                                                            {capitalizeFirstLetter(feedtype)}
                                                        </Nav.Link>
                                                    </Nav.Item>
                                                })
                                            }
                                        </Nav>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="w-100">
                            <h6 className="fs-7 mb-3">Enter {selectedFilterTab}</h6>
                            <div className="card card-custom gutter-b">
                                <div className="card-body px-xxl-5">
                                    <div className="">
                                        <Tab.Content>
                                            <Tab.Pane eventKey={"Hashtag"}>
                                                <div className="mb-2 mb-xl-3">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(2)}
                                                        </span>
                                                        <InputField className={`form-control form-control-lg fw-bold px-9 ${error ? 'is-invalid' : ''}`} name={"hashtag"} placeholder="Enter hashtag" value={hashtag} onChangeHandler={this.onChangeHandler} />
                                                        {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                {/* <div className="pb-2">
                                                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                        <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                            <span className="form-check-label">Manually approve posts before making them public?</span>
                                                        </CheckBoxField>
                                                    </label>
                                                </div> */}
                                            </Tab.Pane>
                                            <Tab.Pane eventKey={"Handle"}>
                                                <div className="mb-2 mb-xl-3">
                                                    <div className="position-relative w-100 mb-3" style={{ zIndex: 0 }} >
                                                        <span className="h-44px w-44px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-4 ps-1 d-flex">
                                                            {this.feedIcon(1)}
                                                        </span>
                                                        <InputField className={`form-control form-control-lg fw-bold px-9 ${handleError ? 'is-invalid' : ''}`} name={"handle"} placeholder="Enter handle" value={handle} onChangeHandler={this.onChangeHandler} />
                                                        {handleError ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                {/* <div className="pb-2">
                                                    <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                                        <CheckBoxField isChecked={moderation} onChangeHandler={this.onCheckHandler} name={"moderation"}>
                                                            <span className="form-check-label">Manually approve posts before making them public?</span>
                                                        </CheckBoxField>
                                                    </label>
                                                </div> */}
                                            </Tab.Pane>
                                            <ShowHideOption onChangeHandler={this.onChangeHandler}/>
                                        </Tab.Content>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Tab.Container>
                <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} />
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Flickr)
